@import url("components/index.scss");
@import url("pages/index.scss");

.auth-popup {
    margin: auto;
    width: 340px;
    background: #fff;
    box-shadow: 0 0 6px #00000024;
    border-radius: 12px;
}

.auth-container {
    padding: 30px;
}

.auth-header {
    font-family: Helvetica, sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0;
    text-align: center;
}

.form {
    margin: 40px 0 0;
}

main {
    display: flex;
    height: 100%;
}

body {
    margin: 0;
}
